<template>
  <div class="re-row-box">
    <div class="word" :style="{ backgroundColor: signBgColor }">
      <div>{{word.word}}</div>
      <div>
        <yinbiao :y="word.explain.f" :file="word.file"></yinbiao>
      </div>
      <div>{{word.explain.e}}</div>
    </div>
    <div class="sign-btn" :style="{zIndex:signOpacity?'-1':'6',opacity:signOpacity?'0':'1'}"  @click="signBtn"></div>
    <div class="sign-box">
      <div
        :style="{ right: right[0], opacity: signOpacity }"
        class="sign-none"
        @click="sign('transparent')"
      ></div>
      <div
        :style="{ right: right[3], opacity: signOpacity }"
        class="sign-green"
        @click="sign('#28CA41')"
      ></div>
      <div
        :style="{ right: right[2], opacity: signOpacity }"
        class="sign-yellow"
        @click="sign('#FFBD2E')"
      ></div>
      <div
        :style="{ right: right[1], opacity: signOpacity }"
        class="sign-red"
        @click="sign('#FF625A')"
      ></div>
    </div>
  </div>
</template>

<script>
import Yinbiao from '../../components/Yinbiao.vue'
export default {
  components:{
    Yinbiao
  },
  props: {
    word: {
      word: Object,
    },
  },
  data() {
    return {
      right: [0, 0, 0, 0],
      signOpacity: 0,
      signBgColor: "none",
    };
  },
  methods: {
    signBtn() {
      if (this.signOpacity) {
        this.signOpacity = 0;
        this.right = [0, 0, 0, 0];
      } else {
        this.signOpacity = 1;
        this.right = ["0px", "30px", "60px", "90px"];
      }
    },
    sign(color) {
      this.signBgColor = color;
      this.signOpacity = 0;
      this.right = [0, 0, 0, 0];
    },
  },
};
</script>

<style>
.re-row-box {
  line-height: 36px;
  line-height: 20px;
  width: 100%;
  position: relative;
  padding: 10px 0;
}
.word {
  width: 100%;
  display: flex;
  height: auto;
}
.word>div:nth-child(1){
  flex: 0 0 150px;
}
.word>div:nth-child(2){
  flex: 0 0 140px;
}
.sign-btn {
  transition: all 0.8s;
  cursor: pointer;
  position: absolute;
  left: -40px;
  top: 6px;
  height: 30px;
  width: 30px;
  background-image: url(../../assets/star.png);
  background-size: 100% 100%;
}
.sign-box {
  position: absolute;
  top: 11px;
  left: -142px;
  height: 20px;
  width: 130px;
  padding-right: 10px;
}
.sign-box > div {
  cursor: pointer;
  height: 20px;
  width: 20px;
  border-radius: 10px;
  position: absolute;
  top: 0;
  transition: all 0.8s;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.2);
}
.sign-box > div:hover {
  box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.2);
}
.sign-none {
  z-index: 4;
  background-image: url(../../assets/none.png);
  background-size: 100% 100%;
}
.sign-green {
  z-index: 3;
  background-color: #28ca41;
}
.sign-yellow {
  z-index: 2;
  background-color: #ffbd2e;
}
.sign-red {
  z-index: 1;
  background-color: #ff625a;
}
</style>