import { find as find_study,create as create_study } from '../api/study'
import { findAll as findAll_bing } from '../api/bing'
import {dateFormat} from './date';

export default new class {
    data=[{
        id:1,
        explain:{
            f:"",
            e:"",
        },
        file:""
    }];
    reviews = [];       //所有已经学习过的数组
    reviewsTody = [];   //今日要复习的数组
    studyData = [];     //现在要学习的数组
    maxLength = 100;     //设置最大单词数量
    index = 0;          //现在要学习的下标

    constructor() {
        this.getReviews();
    }

    getReviews() {
        let temp=localStorage.getItem('data')||false;
        if(temp){
            console.log("本地获取了data")
            this.data=JSON.parse(temp);
            find_study().then(
                res2 => {
                    if (res2.data.length) {
                        this.reviews = res2.data;
                        let t1 = res2.data;
                        let t2 = t1[t1.length - 1].indexs;
                        this.index = t2[t2.length - 1] + 1;
                        this.getTodyReviews();
                    } else {
                        this.index = 0;
                    }
                }
            )
        }else{
            findAll_bing().then(res=>{
                this.data=res.data;
                localStorage.setItem('data',JSON.stringify(res.data));
                find_study().then(
                    res2 => {
                        if (res2.data.length) {
                            this.reviews = res2.data;
                            let t1 = res2.data;
                            let t2 = t1[t1.length - 1].indexs;
                            this.index = t2[t2.length - 1] + 1;
                            this.getTodyReviews();
                        } else {
                            this.index = 0;
                        }
                    }
                )
            })
        }
        

    }

    getTodyReviews() {
        let Ebbinghaus = [0, 1, 2, 4, 7, 15];
        let reviewsDay = [];
        for (let i of Ebbinghaus) {
            reviewsDay.push(dateFormat("YYYY-mm-dd", new Date(Date.now() - i * 24 * 60 * 60 * 1000)));
        }
        let temp = [];
        for (let i of reviewsDay) {
            for (let j of this.reviews) {
                if (i === j.date) {
                    temp.push(j);
                }
            }
        }
        this.reviewsTody = temp;
    }

    addStudy() {
        if (this.studyData.length < this.maxLength) {
            this.studyData.push(this.index);
            if (this.studyData.length !== this.maxLength) {
                this.index++;
            }
        } else {
            alert("已经到达今日学习的最大单词数量了！")
        }
    }
    next() {
        if ((this.index + 1) !== this.data.length) {
            this.index++;
        } else {
            alert("恭喜你学完了！")
        }
    }
    preserveStudy(day = new Date()) {
        create_study(new Date(),this.studyData).then(res=>alert('上传成功'))
    }
}