<template>
  <div id="review">
    <div>
      <p>今日复习</p>
      <button
        v-for="i in reviewsTody"
        :key="i.id"
        @click="reviewWords(i.indexs)"
      >
        {{ i.date }}复习
      </button>
    </div>

    <div>
      <p>所有复习</p>
      <button
        v-for="i in reviewsAll"
        :key="i.id"
        @click="reviewWords(i.indexs)"
      >
        {{ i.date }}复习
      </button>
    </div>
    <div id="words" :style="{ top: wordsTop }">
      <div @click="wordsTop = '100%'">关闭</div>
      <div class="word-list">
        <re-li v-for="i in words" :key="data[i].id" :word="data[i]"></re-li>
      </div>
    </div>
  </div>
</template>

<script>
import database from "../../utils/database";
import ReLi from "./Re-Li.vue";
export default {
  components: {
    ReLi,
  },
  data() {
    return {
      review: database,
      words: [],
      wordsTop: "100%",
    };
  },
  methods: {
    reviewWords(arr) {
      this.words = arr;
      this.wordsTop = "0";
    },
  },
  computed: {
    data() {
      return this.review.data;
    },
    reviewsAll() {
      return this.review.reviews;
    },
    reviewsTody() {
      return this.review.reviewsTody;
    },
  },
};
</script>

<style lang="less">
#review {
  height: 100%;
  width: 100%;
  position: relative;
  overflow: hidden;
}
.word-list>div:nth-child(10n){
  border-bottom: solid 1px black;
  padding-bottom: 60px;
  margin-bottom: 60px;
}
.word-list>div:nth-child(5n):not(:nth-child(10n)){
  border-bottom: solid 1px gray;
}
.word-list>div:nth-child(50n){
  border-bottom: solid 2px red;
  padding-bottom: 200px;
  margin-bottom: 200px;
}
.word-list>div:last-child{
  border-bottom: none;
  margin-bottom: none;
}
#words {
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #F5F5D5;
  position: absolute;
  left: 0;
  transition: top 1s;
  & > div:last-child{
    width: 1000px;
    margin: 0 auto;
    padding: 15px 0;
  }
  & > div:first-child {
    position: fixed;
    right: 20px;
    top: 10px;
    cursor: pointer;
  }
}
</style>