import {studyApi} from '../utils/ajax'
import {dateFormat} from '../utils/date'

export function find(){
    return studyApi.get(
        "find"
    )
}

export function create(myDate=new Date(), indexs=[]){
    let date=dateFormat("YYYY-mm-dd",myDate);
    return studyApi.post("create",{
        date,indexs
    })
}