<template>
  <div class="yinbiao-box" @mouseenter="playMp3" @mouseleave="pauseMp3">
    <p>
        {{yinbiao}}
    </p>
    <audio ref="mp3" loop>
      <source :src="file" type="audio/mp3" />
    </audio>
  </div>
</template>

<script>
export default {
  props: {
    y: {
      type: String,
    },
    file: {
      type: String,
    },
  },
  methods: {
    pauseMp3() {
      this.$refs.mp3.pause();
    },
    playMp3() {
      this.$refs.mp3.currentTime = 0;
      this.$refs.mp3.play();
    },
  },
  computed:{
      yinbiao(){
          return this.y.replace("\\","'");
      }
  }
};
</script>

<style>
    .yinbiao-box{
        width: 100%;
        cursor: pointer;
    }
</style>