<template>
  <div class="box">
    <div class="left">
      {{ studyData.length }}/{{ maxLength }}
      <div v-if="studyData.length !== maxLength">
        <div style="margin: 10px">
          <p>
            <span @mouseleave="showMean = false" @mouseover="showMean = true">{{
            data[index].word
          }}</span>

          <span v-show="showMean">{{ data[index].explain.e }}</span>
          </p>
            <yinbiao :file="data[index].file" :y="data[index].explain.f" :key="data[index].file"></yinbiao>
        </div>
        <button @click="add" style="margin: 0 10px">不认识</button>
        <button @click="next">认识</button>
      </div>
      <div v-else>
        <h3>恭喜你已经完成今日任务，点击上传同步到数据库</h3>
        <button @click="upd">上传</button>
      </div>
    </div>

    <div class="line"></div>

    <div class="right">
      <ul>
        <li v-for="i of studyData" :key="i">
          <span>{{ data[i].word }}</span>
          <span>{{ data[i].explain.e }}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import database from "../../utils/database";
import Yinbiao from "../../components/Yinbiao.vue";
export default {
  components:{
    Yinbiao
  },
  data() {
    return {
      study: database, // 注意vue的data对外部引用的监听，基本类型便不会监听，只是简单的赋值,相当于独立的两个部分。
      //如果是引用类型，则是复制引用的指针，会监听,
      showMean: false,
    };
  },
  methods: {
    pauseMp3(){
      this.$refs.mp3.pause();
    },
    playMp3() {
      this.$refs.mp3.currentTime = 0;
      this.$refs.mp3.play();
    },
    add() {
      database.addStudy();
    },
    upd() {
      database.preserveStudy();
    },
    next() {
      database.next();
    },
  },
  computed: {
    data() {
      return this.study.data;
    },
    studyData() {
      return this.study.studyData;
    },
    maxLength() {
      return this.study.maxLength;
    },
    index() {
      return this.study.index;
    },
  },
};
</script>

