  .right ul {
    padding: 0 15px;
    list-style: none;
    & li {
      &:nth-child(10n) {
        border-bottom: solid 2px gainsboro;
        padding-bottom: 20px;
        margin-bottom: 20px;
      }
      &:last-child {
        border: none;
      }
      display: flex;
      & span:first-child {
        flex: 1;
      }
      & span:last-child {
        flex: 3;
      }
    }
  }
}
</style>