import axios from 'axios';
export const bingApi = axios.create({
    baseURL: "/BingOpt",
    responseType: 'json',
    transformResponse(res) {
        if (res.code) {
            throw new Error(res.msg)
        } else {
            return res.data
        }
    }
});

